import React, { useState, useEffect } from 'react';
import { useForm, ValidationError } from '@formspree/react';
import '../styles/ContactForm.css';

function ContactForm() {
  const [state, handleSubmit] = useForm("mqaloygw");
  const [showConfirmation, setShowConfirmation] = useState(false);
  
  const handleFormSubmit = async (e) => {
    e.preventDefault(); // Prevent default form submission
    await handleSubmit(e);
  };
  
  useEffect(() => {
    if (state.succeeded) {
      setShowConfirmation(true);
      
      // Save current scroll position when form succeeds
      const currentScrollY = window.scrollY;
      sessionStorage.setItem('scrollPosition', currentScrollY.toString());
    }
  }, [state.succeeded]);
  
  // Function to handle OK button click
  const handleOkayClick = () => {
    window.location.reload();
  };
  
  // Restore scroll position on component mount (after refresh)
  useEffect(() => {
    const savedScrollPosition = sessionStorage.getItem('scrollPosition');
    if (savedScrollPosition) {
      window.scrollTo(0, parseInt(savedScrollPosition));
      sessionStorage.removeItem('scrollPosition'); // Clean up
    }
  }, []);

  return (
    <div className="contact-container">
      <form className="contact-form" onSubmit={handleFormSubmit}>
        <div className="form-row">
          <div className="input-container">
            <input
              placeholder="First Name"
              required
              type="text"
              name="firstName"
            />
            <ValidationError 
              prefix="First Name" 
              field="firstName"
              errors={state.errors}
            />
          </div>
          <div className="input-container">
            <input
              placeholder="Last Name"
              required
              type="text"
              name="lastName"
            />
            <ValidationError 
              prefix="Last Name" 
              field="lastName"
              errors={state.errors}
            />
          </div>
          <div className="input-container">
            <input
              placeholder="Phone Number"
              required
              type="tel"
              name="phone"
            />
            <ValidationError 
              prefix="Phone" 
              field="phone"
              errors={state.errors}
            />
          </div>
          <div className="input-container">
            <input
              placeholder="Email"
              required
              type="email"
              name="email"
            />
            <ValidationError 
              prefix="Email" 
              field="email"
              errors={state.errors}
            />
          </div>
        </div>
        <div className="form-row">
          <div className="input-container textarea-container">
            <textarea
              name="websiteDescription"
              placeholder="Describe your kind of website..."
              rows="5"
              required
            />
            <ValidationError 
              prefix="Website Description" 
              field="websiteDescription"
              errors={state.errors}
            />
          </div>
        </div>
        <button type="submit" className="submit-btn" disabled={state.submitting}>
          {state.submitting ? 'Sending...' : 'Send Message'}
        </button>
      </form>

      {/* Glass Confirmation Popup */}
      {showConfirmation && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0, 0, 0, 0.3)',
          backdropFilter: 'blur(8px)',
          WebkitBackdropFilter: 'blur(8px)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000,
          animation: 'fadeIn 0.3s ease-out'
        }}>
          <div style={{
            background: 'rgba(255, 255, 255, 0.15)',
            backdropFilter: 'blur(20px)',
            WebkitBackdropFilter: 'blur(20px)',
            border: '1px solid rgba(255, 255, 255, 0.2)',
            borderRadius: '20px',
            padding: window.innerWidth <= 768 ? '30px 25px' : '40px 50px',
            textAlign: 'center',
            boxShadow: '0 8px 32px rgba(0, 0, 0, 0.1)',
            animation: 'slideIn 0.4s ease-out',
            maxWidth: '400px',
            width: window.innerWidth <= 768 ? '80%' : '90%',
            margin: window.innerWidth <= 768 ? '20px' : '0'
          }}>
            <div style={{
              marginBottom: '20px',
              display: 'flex',
              justifyContent: 'center',
              animation: 'checkScale 0.5s ease-out 0.2s both'
            }}>
              <svg width="40" height="40" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <circle cx="12" cy="12" r="12" fill="#00C851"/>
                <path d="m9 12 2 2 4-4" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </div>
            <p style={{
              fontFamily: 'Montserrat, sans-serif',
              fontSize: window.innerWidth <= 768 ? '20px' : '24px',
              fontWeight: '600',
              color: 'white',
              margin: '0 0 10px 0',
              textShadow: '0 2px 4px rgba(0, 0, 0, 0.3)'
            }}>Message sent successfully!</p>
            <p style={{
              fontFamily: 'Montserrat, sans-serif',
              fontSize: window.innerWidth <= 768 ? '14px' : '16px',
              fontWeight: '400',
              color: 'rgba(255, 255, 255, 0.9)',
              margin: '0',
              textShadow: '0 1px 2px rgba(0, 0, 0, 0.3)'
            }}>We'll get back to you soon.</p>
            <button 
              onClick={handleOkayClick}
              style={{
                marginTop: '20px',
                padding: '10px 30px',
                background: 'rgba(255, 255, 255, 0.2)',
                border: '1px solid rgba(255, 255, 255, 0.3)',
                borderRadius: '25px',
                color: 'white',
                fontFamily: 'Montserrat, sans-serif',
                fontSize: '14px',
                fontWeight: '500',
                cursor: 'pointer',
                transition: 'all 0.3s ease',
                backdropFilter: 'blur(10px)',
                WebkitBackdropFilter: 'blur(10px)'
              }}
              onMouseEnter={(e) => {
                e.target.style.background = 'rgba(255, 255, 255, 0.3)';
                e.target.style.transform = 'translateY(-2px)';
              }}
              onMouseLeave={(e) => {
                e.target.style.background = 'rgba(255, 255, 255, 0.2)';
                e.target.style.transform = 'translateY(0)';
              }}
            >
              Okay
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default ContactForm;